@import url('https://fonts.googleapis.com/css?family=Lato&display=swap');

/* * {
  box-sizing: border-box;
  font-family: 'Lato', sans-serif;
} */

:root {
  --color1: #1B2430;
  --color2: #51557E;
  --color3: #816797;
  --color4: #D6D5A8;
}

body {
  margin: 0;
  font-family: 'Lato', sans-serif;
  background-color: var(--color1);
  color: white;
}

.container {
  text-align: center;
}

.nav {
  background-color: var(--color1);
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: stretch;
  gap: 2rem;
  padding: 0 1rem;
}

.site-title {
  font-size: 2rem;
}

.nav ul {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  /* gap: 1rem; */
}
/* replaces gap */
.nav ul > * + * { margin-left: 1rem;}

.nav a {
  color: inherit;
  text-decoration: none;
  height: 100%;
  display: flex;
  align-items: center;
  padding: 0.25rem;
}

.nav li {
  padding-bottom: 10px;
  border: solid 2px transparent;
}

.nav li:hover {
  background-color: var(--color1);
  border-radius: 25%;
  border: solid 2px black;
  padding-bottom: 10px;

}

.nav li.active {
  color: black;
  background-color: var(--color4);
  border-radius: 25%;
  border: solid 2px black;
  padding-bottom: 10px;
}

/* Home Page */

/* Body */
.homeBody {
  margin-top: 40vh;
}

/* Box to keep blinker from going to edge of page */
.typeBox {
  display: inline-block;
}

/* Typewriter for h1 */
.typewriter {
  overflow: hidden; /* Ensures the content is not revealed until the animation */
  border-right: .15em solid transparent; /* The typwriter cursor */
  white-space: nowrap; /* Keeps the content on a single line */
  margin: 0 auto; /* Gives that scrolling effect as the typing happens */
  letter-spacing: .15em; /* Adjust as needed */
  animation: 
    typing 3.5s steps(40, end),
    blink-caret .75s step-end 4.75;
}

/* Typewriter for h3 */
.typewriter2 {
  overflow: hidden; /* Ensures the content is not revealed until the animation */
  border-right: .15em solid orange; /* The typwriter cursor */
  white-space: nowrap; /* Keeps the content on a single line */
  margin: 0 auto; /* Gives that scrolling effect as the typing happens */
  letter-spacing: .15em; /* Adjust as needed */
  visibility: hidden;
  animation: 
    hide-second 3.5s infinite,
    typing 3.5s steps(40, end),
    blink-caret2 .75s step-end infinite;
  animation-delay: 3.5s;
}

/* The typing effect */
@keyframes typing {
  from { width: 0; }
  to { width: 100% }
 
}

/* The typewriter cursor effect */
@keyframes blink-caret {
  from, to { border-color: transparent }
  50% { border-color: orange; }
  100% { border-color: transparent }
}

@keyframes blink-caret2 {
  from, to { border-color: transparent }
  50% { border-color: orange; }
}

/* Hides content until h1 has loaded then displays h3 */
@keyframes hide-second {
  from, to { visibility: hidden; }
  50% { visibility: visible; }
}

.social {
  visibility: hidden;
  overflow: hidden;
  animation: 
  loadIcons 3s ease-in 8s forwards;
}

@keyframes loadIcons {
  from, to { visibility: hidden; }
  50% { visibility: visible; }
  100% { visibility: visible; }
}